.tabs {
  position: relative;
  background: #f8f9fc;
}

.tabs__wrapper {
  margin: 0 auto;
  max-width: 1160px;
  padding: 120px 0;

  @include vp-1199 {
    padding: 153px 0 100px;
  }

  @include vp-767 {
    padding: 110px 0 70px;
  }
}

.tabs__wrapper h2 {
  margin: 0;
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  letter-spacing: 1.62px;
  line-height: 65px;
  text-align: center;

  @include vp-1199 {
    font-size: 42px;
    letter-spacing: 1.26px;
  }

  @include vp-767 {
    font-size: 32px;
    letter-spacing: 0.96px;
  }
}

.tabs__controls {
  position: relative;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 auto 39px auto;

  @include vp-1199 {
    margin-bottom: 45px;
  }

  @include vp-767 {
    margin-bottom: 35px;
  }
}

.tabs__controls button {
  position: relative;
  display: inline-block;
  padding: 16px 24px;
  border: 0;
  color: rgba(16, 38, 84, 0.5);
  background-color: transparent;
  font-size: 20px;
  font-weight: 400;
  line-height: 30px;
  cursor: pointer;

  @include vp-1199 {
    padding: 13px 28px;
    font-size: 18px;
    line-height: 27px;
  }

  @include vp-767 {
    padding: 8px 15px 14px;
    font-size: 16px;
    line-height: 24px;

    &:nth-child(2n) {
      padding: 8px 45px 14px;

      &::before {
        width: 50%;
        left: 25%;
      }
    }

    &:nth-child(2n+1) {
      padding: 8px 0 14px;
    }
  }

  &::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: rgba(120, 120, 120, 0.2);
  }

  &::before {
    position: absolute;
    content: "";
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #d10a42;
    transform: scale(0);
    transform-origin: center;
  }

  &:hover,
  &:focus-visible {
    color: #102654;
    outline: none;

    &::before {
      transform: scale(1);
    }
  }

  &:focus {
    outline: none;
  }

  &:active {
    color: #102654;

    &::before {
      transform: scale(1);
    }
  }

  &:disabled {
    color: rgba(16, 38, 84, 0.2);
    cursor: default;

    &::after {
      width: 0;
    }

    &::before {
      width: 0;
    }
  }
}

.tabs__controls button.is-active {
  color: #102654;

  &::before {
    transform: scale(1);
  }
}

.tabs__image img {
  position: absolute;
  bottom: -160px;
  right: -183px;

  @include vp-1199 {
    bottom: -130px;
    right: -80px;
    width: 423px;
    height: 444px;
  }

  @include vp-767 {
    display: none;
  }
}
